header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  backdrop-filter: blur(4px);
  background-color: white;

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 55px;
    padding: 0 20px;

    @media (min-width: 768px) {
      height: 70px;
      padding: 0 30px;
    }
    @media (min-width: 1024px) {
      padding: 0 50px;
    }

    figure {
      cursor: pointer;

      img {
        width: 25px;
        height: 25px;

        @media (min-width: 768px) {
          width: 30px;
          height: 30px;
        }
      }
    }

    .box {
      display: flex;
      align-items: center;
    }

    nav.desktop {
      display: none;

      @media (min-width: 1024px) {
        display: flex;
        align-items: center;
      }

      ul {
        display: flex;

        li {
          margin-right: 40px;
          opacity: 0.5;

          &.active {
            opacity: 1;
          }

          &:hover {
            opacity: 0.88;
          }

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }

    $hamburgerWidth: 25px;
    $hamburgerHeight: 17px;
    $hamburgerColor: black;
    .hamburger {
      position: relative;
      width: $hamburgerWidth;
      height: $hamburgerHeight;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      cursor: pointer;

      @media (min-width: 1024px) {
        display: none;
      }

      & > div {
        width: 100%;
        height: 1px;
        background-color: $hamburgerColor;
      }
    }

    .mobile-nav-back {
      position: absolute;
      top: 0%;
      right: -100%;
      width: 100vw;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.4);

      &.active {
        right: 0;
      }

      @media (min-width: 1024px) {
        display: none;
      }
    }

    .mobile-nav {
      position: absolute;
      top: 0%;
      right: -100%;
      min-width: 300px;
      max-width: 300px;
      height: 100vh;
      background-color: #ffffff;
      transition: 0.2s;

      &.active {
        right: 0;
      }

      @media (min-width: 1024px) {
        display: none;
      }

      & div.close {
        position: absolute;
        top: 19px;
        right: 21px;
        width: 26px;
        height: 18px;
        cursor: pointer;

        &:after,
        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          display: block;
          width: 24px;
          height: 1px;
          background-color: black;
        }

        &:after {
          transform: translate(-50%, -50%) rotate(45deg);
        }

        &:before {
          transform: translate(-50%, -50%) rotate(-45deg);
        }
      }

      ul {
        margin-top: 160px;
        margin-left: 50px;

        li {
          margin-bottom: 40px;
          opacity: 0.5;

          &.active {
            opacity: 1;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }

