section#staking-info {
  padding: 0 12px;

  .info-container {
    z-index: 999;

    .wrapper {
      // padding-bottom: 100px;
      padding-top: 40px;
    }
    .title-set {
      text-align: center;

      img {
        width: 70px;
        margin: 0 auto 22px;
      }

      h2 {
        font-size: 22px;
        margin-bottom: 5px;
      }

      strong.return {
        font-weight: bold;

        b {
          color: lightcoral;

          &::before {
            content: " 사이클당 ";
            color: #0088cc;
            padding: 0 4px;
          }
        }
      }
    }

    a.go-staking {
      display: block;
      width: 100%;
      height: 40px;
      line-height: 40px;
      background-color: rgb(106, 190, 241);
      border: 0;
      border-radius: 4px;
      font-size: 14px;
      color: #eee;
      margin: 30px auto 0;
      text-align: center;
      cursor: pointer;

      @media (min-width: 768px) {
        font-size: 16px;
        max-width: 300px;
      }
    }

    .info-set {
      background-color: rgb(245, 245, 245);
      padding: 40px 24px;
      // margin: 40px -16px 0;
      margin-top: 40px;
      border-radius: 4px;

      @media (min-width: 768px) {
        padding: 40px;
        margin: 60px 0 20px;
      }

      div {
        line-height: 1.8;

        strong {
          color: rgb(43, 94, 233);
          font-size: 17px;
          font-weight: bold;

          @media (min-width: 768px) {
            font-size: 19px;
          }

          span {
            font-weight: normal;
            color: #000;
            font-size: 14px;

            @media (min-width: 768px) {
              font-size: 15px;
            }
          }
        }

        .time {
          padding-bottom: 20px;

          time {
            font-size: 24px;
            font-weight: bold;
          }
        }
      }
    }

    .faq-set {
      padding-top: 40px;

      h4 {
        font-size: 16px;
        padding: 0 10px;
      }

      ul.accordion {
        padding-top: 8px;
        li.list {
          margin-top: 8px;

          strong.title {
            display: block;
            position: relative;
            padding: 12px 60px 12px 16px;
            background: #f8f8f9;
            border-radius: 4px;
            font-weight: 400;
            line-height: 25px;
            color: #79818f;
            -webkit-transition: color 0.3s;
            transition: color 0.3s;
            cursor: pointer;

            img {
              position: absolute;
              top: 50%;
              right: 19px;
              transform: translate(0, -50%);
              opacity: 0.5;
              -webkit-transition: -webkit-transform 0.3s;
              transition: -webkit-transform 0.3s;
              transition: transform 0.3s;
              transition: transform 0.3s, -webkit-transform 0.3s;
            }
          }

          p.description {
            overflow: hidden;
            height: 0;
            padding: 0 16px;
            line-height: 25px;
            color: #484d55;
            -webkit-transition: height 0.3s, padding 0.3s;
            transition: height 0.3s, padding 0.3s;
          }

          &.on {
            strong.title {
              color: #111111;
              font-weight: bold;
            }

            img {
              opacity: 1 !important;
              -webkit-transform: translate(0, -50%) rotate(90deg) !important;
              transform: translate(0, -50%) rotate(90deg) !important;
            }

            p.description {
              height: auto;
              padding: 12px 16px 16px;
            }
          }
        }
      }
    }

    // button {
    //   display: block;
    //   width: 140px;
    //   height: 36px;
    //   line-height: 36px;
    //   background-color: rgb(106, 190, 241);
    //   border: 0;
    //   border-radius: 4px;
    //   font-size: 14px;
    //   color: #eee;
    //   margin: 0 auto;
    //   cursor: pointer;

    //   @media (min-width: 768px) {
    //     font-size: 16px;
    //   }
    // }
  }
}
